@import "_variables.scss";

html {
  height: 100%;
}

body {
  background-color: #2a2a2a;
  color: $text-color;
}

@mixin flexCenter($direction) {
  display: flex;
  align-content: center;
  align-items: center;
  flex-direction: $direction;
}

@mixin underline-Animation {
  .main-div__title {
    position: relative;
    color: #fff;
    display: inline-block;
    text-decoration: none;
  }

  .main-div__title:hover {
    color: #fff;
  }

  .main-div__title::after {
    content: "";
    position: absolute;
    display: inline-block;
    width: 100%;
    height: 2.5px;
    border-radius: 10px;
    bottom: 0;
    left: 0;
    background-color: #fff;
    transform: scaleX(0);
    transition: transform 0.3s ease;
  }

  .main-div__title:hover::after {
    transform: scaleX(1);
  }
}

.page {
  background-color: #161616;
  width: 25%;
  height: 90vh;
  margin: 2em auto;
  border-radius: 10px;
  font-family: "IBM Plex Sans", sans-serif;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

@function font-map($key) {
  @return map-get($font-weight, $key);
}

@function page-font-size($font__choice) {
  @return map-get($font__size, $font__choice);
}

.info-div {
  display: flex;
  @include flexCenter(column);

  .img__item {
    background-color: $color-accent;
    overflow: hidden;
    border-radius: 10px 10px 0px 0px;
    height: 250px;

    img {
      width: 375px;
      height: 250px;
      object-fit: cover;
      border-radius: 10px 10px 0px 0px;
      transition: transform 750ms cubic-bezier(0.5, 0, 0, 1),
        opacity 250ms linear;
    }

    img:hover {
      transform: scale(1.2);
      opacity: 0.5;
    }
  }

  h1 {
    font-weight: font-map(medium);
    font-size: page-font-size(big);
    text-align: center;
  }

  h5 {
    font-weight: font-map(regular);
    font-size: page-font-size(medium);
    text-align: center;
    margin: -1em auto;
    color: $info__desc-color;
  }

  h6 {
    font-size: page-font-size(small);
    font-weight: font-map(regular);
    text-align: center;
    margin: 2.5em auto;
    margin-bottom: 1.5em !important;
  }

  .email-link {
    margin-top: auto;

    .fa {
      padding-right: 0.5em;
      font-size: 15px;
    }

    .email-button {
      position: relative;
      margin-top: -8em !important;
      width: 21em;
      height: 38px;
      border-radius: 7px;
      font-size: page-font-size(medium) !important;
      font-weight: 400;
      background-color: white;
      border: 2px solid black;
      color: black;
      padding: 5px 10px;
      text-align: center;
      display: inline-block;
      font-size: 20px;
      margin: 10px 30px;
      cursor: pointer;
    }
  }
}

.main-div {
  margin-left: 1.75em;
  margin-right: 1.75em;
  margin-top: -0.5em;
  @include underline-Animation;

  &__title {
    font-size: page-font-size(big);
  }

  &__para {
    font-size: page-font-size(small);
    margin-top: -10px;
  }
}

.footer {
  margin-top: auto;
  background-color: #000;
  position: sticky;
  border-radius: 0px 0px 10px 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 3.75em;

  .fa {
    margin: 0.65em 0.5em;
    font-size: 1.25em;
    color: #000;
    background-color: grey;
    padding: 3px 3px;
    border-radius: 2.5px;
    margin-bottom: 1em;
  }

  .fa-facebook-f {
    padding: 3px 8px;
  }

  .fa-linkedin {
    padding: 3px 5px;
  }

  .fa-instagram {
    padding: 3px 6px;
  }

  .fa-github {
    padding-right: 5px;
    padding-left: 5px;
  }
}
