html {
  height: 100%;
}

body {
  background-color: #2a2a2a;
  color: #fff;
}

.page {
  background-color: #161616;
  width: 25%;
  height: 90vh;
  margin: 2em auto;
  border-radius: 10px;
  font-family: "IBM Plex Sans", sans-serif;
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.info-div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.info-div .img__item {
  background-color: #16e0bd;
  overflow: hidden;
  border-radius: 10px 10px 0px 0px;
  height: 250px;
}

.info-div .img__item img {
  width: 375px;
  height: 250px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 10px 10px 0px 0px;
  -webkit-transition: opacity 250ms linear, -webkit-transform 750ms cubic-bezier(0.5, 0, 0, 1);
  transition: opacity 250ms linear, -webkit-transform 750ms cubic-bezier(0.5, 0, 0, 1);
  transition: transform 750ms cubic-bezier(0.5, 0, 0, 1), opacity 250ms linear;
  transition: transform 750ms cubic-bezier(0.5, 0, 0, 1), opacity 250ms linear, -webkit-transform 750ms cubic-bezier(0.5, 0, 0, 1);
}

.info-div .img__item img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  opacity: 0.5;
}

.info-div h1 {
  font-weight: 600;
  font-size: 1em;
  text-align: center;
}

.info-div h5 {
  font-weight: 200;
  font-size: 0.75em;
  text-align: center;
  margin: -1em auto;
  color: #daa520;
}

.info-div h6 {
  font-size: 0.6em;
  font-weight: 200;
  text-align: center;
  margin: 2.5em auto;
  margin-bottom: 1.5em !important;
}

.info-div .email-link {
  margin-top: auto;
}

.info-div .email-link .fa {
  padding-right: 0.5em;
  font-size: 15px;
}

.info-div .email-link .email-button {
  position: relative;
  margin-top: -8em !important;
  width: 21em;
  height: 38px;
  border-radius: 7px;
  font-size: 0.75em !important;
  font-weight: 400;
  background-color: white;
  border: 2px solid black;
  color: black;
  padding: 5px 10px;
  text-align: center;
  display: inline-block;
  font-size: 20px;
  margin: 10px 30px;
  cursor: pointer;
}

.main-div {
  margin-left: 1.75em;
  margin-right: 1.75em;
  margin-top: -0.5em;
}

.main-div .main-div__title {
  position: relative;
  color: #fff;
  display: inline-block;
  text-decoration: none;
}

.main-div .main-div__title:hover {
  color: #fff;
}

.main-div .main-div__title::after {
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 2.5px;
  border-radius: 10px;
  bottom: 0;
  left: 0;
  background-color: #fff;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.main-div .main-div__title:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.main-div__title {
  font-size: 1em;
}

.main-div__para {
  font-size: 0.6em;
  margin-top: -10px;
}

.footer {
  margin-top: auto;
  background-color: #000;
  position: -webkit-sticky;
  position: sticky;
  border-radius: 0px 0px 10px 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 3.75em;
}

.footer .fa {
  margin: 0.65em 0.5em;
  font-size: 1.25em;
  color: #000;
  background-color: grey;
  padding: 3px 3px;
  border-radius: 2.5px;
  margin-bottom: 1em;
}

.footer .fa-facebook-f {
  padding: 3px 8px;
}

.footer .fa-linkedin {
  padding: 3px 5px;
}

.footer .fa-instagram {
  padding: 3px 6px;
}

.footer .fa-github {
  padding-right: 5px;
  padding-left: 5px;
}
